<template>
  <div>
    <h1>테스트 영역</h1>
    <h5>현재는 {{ display }} 위 까지만 노출됩니다.</h5>
    <div v-if="isLoading" class="loadingbar">
      <LoadingBar />
    </div>
    <div class="search-box">
      <input v-model="searchQuery" placeholder="키워드를 검색하세요." />
      <button class="search" @click="searchNaverShopping">검색</button>
    </div>
    <ul>
      <li v-for="(item, index) in searchResults" :key="index" class="item">
        <div class="ranking">{{ index + 1 }}</div>
        <img
          :src="item.image"
          :alt="item.title.replace(/<b>|<\/b>/g, '')"
          class="product-image"
          @mouseover="showZoomedImage(item.image, index)"
          @mouseout="hideZoomedImage"
          @mousemove="updateZoomedImagePosition"
        />
        <div
          v-if="zoomedImage && zoomedImagePosition === index"
          class="zoomed-image-container"
        >
          <img :src="zoomedImage" class="zoomed-image" />
        </div>
        <div class="product-info">
          <p class="store-info">
            {{ item.mallName === "네이버" ? "가격비교" : item.mallName }}
            <span v-if="item.mallName === '네이버'"></span>
          </p>
          <a :href="item.link" target="ank" rel="noopener noreferrer">
            <span v-html="item.title.replace(/<b>|<\/b>/g, '')"></span>
          </a>
          <button class="A" :class="{ 'B': item.isValueChanged }" @click="updateButtonText(item)">
            {{ item.buttonText || '보기' }}
          </button>
          <p class="mid-info">MID : {{ item.productId }}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";
import LoadingBar from "@/components/LoadingBar.vue";

async function fetchResults(keyword, start) {
  const clientId = "SliOUwwJyx40KDhySwCf";
  const clientSecret = "r1Z3AkEecz";
  const apiUrl = "/api/v1/search/shop.json";

  try {
    const response = await axios.get(apiUrl, {
      headers: {
        "X-Naver-Client-Id": clientId,
        "X-Naver-Client-Secret": clientSecret,
      },
      params: {
        query: keyword,
        start: start,
        display: 100,
      },
    });

    return response.data.items;
  } catch (error) {
    console.error(error);
    return [];
  }
}
// 상품 목록의 수 불러오기
async function fetchAllResults(keyword) {
  let allItems = [];

  for (let start = 1; start <= 1000; start += 100) {
    const items = await fetchResults(keyword, start);
    allItems = allItems.concat(items);
  }

  return allItems;
}

// export default --- export default --- export default --- export default --- export default --- export default --- export default
export default {
  components: {
    LoadingBar,
  },
  data() {
    return {
      searchQuery: "",
      searchResults: [],
      display: 1000,
      zoomedImage: null,
      zoomedImagePosition: null,
      isLoading: false,
      buttonText: "보기",
    };
  },
  // methods --- methods --- methods --- methods --- methods --- methods --- methods --- methods --- methods --- methods --- methods
  methods: {
    // 이미지를 담당하는 영역
    updateZoomedImagePosition(event) {
      if (this.zoomedImage) {
        const zoomedImageContainer = this.$el.querySelector(".zoomed-image-container");
        zoomedImageContainer.style.left = event.clientX + 30 + "px";
        zoomedImageContainer.style.top = event.clientY + 30 + "px";
      }
    },
    showZoomedImage(image, index) {
      this.zoomedImage = image;
      this.zoomedImagePosition = index;
    },
    hideZoomedImage() {
      this.zoomedImage = null;
    },
    // 상품의 아이템을 가져오는 영역
    async searchNaverShopping() {
      if (!this.searchQuery) {
        return;
      }
      this.isLoading = true;
      const allItems = await fetchAllResults(this.searchQuery);
      this.searchResults = allItems.slice(0, this.display);
      this.isLoading = false;
    },
    async updateButtonText(item) {
      const popularDegree = await this.fetchPopularDegree(item.productId);
      item.buttonText = popularDegree;
      item.isValueChanged = true; // 버튼의 색깔을 변경하기 위한 플래그 설정
    },
    async fetchPopularDegree(productId) {
      // 상품 페이지 URL 설정
      const serverUrl = `https://port-0-ykad-koh2xlikf0dph.sel4.cloudtype.app/product/${productId}`;

      // 페이지 데이터 가져오기 (서버에서 처리, CORS 문제 없음)
      const response = await axios.get(serverUrl);

      // popularDegree 값 반환
      return response.data.popularDegree;
    },
  },
};
</script>

<style scoped>
a {
  color: #303030;
  text-decoration: none;
}
.zoomed-image-container {
  position: fixed;
  left: 0px;
  top: 0;
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.9);
  border: 1px solid #ccc;
  border-radius: 5px;
  z-index: 10;
}
.zoomed-image {
  max-width: 200px;
  max-height: 200px;
}
.A {
  width: 80px;
  height: 30px;
  margin-left: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: -40px;
  margin-right: -12px;
}
.B {
  background-color: #db6334;
}
.mid-info {
  font-size: 14px;
  color: #777;
  position: absolute;
  right: 0;
  bottom: -15px;
}
input {
  flex: 1;
  width: 450px;
  padding: 10px;
  height: 25px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 10px;
}
.loadingbar {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 5%;
  left: 50%;
  transform: translateX(-50%);
}
</style>

<style>
::placeholder {
  color: #303030a1;
  font-size: 14px;
}
h1 {
  margin: auto;
  align-items: center;
  margin-top: 100px;
  margin-bottom: -30px;
  padding: 30px;
  color: #3498db;
}
.search-box {
  margin: auto;
  max-width: 30%;
  display: contents;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 10px;
  padding: 20px;
}
button {
  padding: 10px 15px;
  font-size: 14px;
  background-color: #3498db;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s;
}
button:hover {
  background-color: #024081;
}

#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 30px;
}
.item {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #ccc;
  margin-bottom: 20px;
  padding: 30px;
  position: relative;
  max-width: 800px;
  margin: auto;
}
.ranking {
  position: absolute;
  left: -35px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 1rem;
  font-weight: bold;
  color: #303030cc;
  width: 25px;
  height: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}
.product-image {
  max-width: 60px;
  max-height: 60px;
  margin-right: 30px;
  vertical-align: middle;
}
.product-info {
  display: flex;
  font-size: 15px;
  flex-direction: column;
  justify-content: space-between;
  flex: 1;
}
.store-info {
  font-size: 14px;
  color: #777;
  margin-left: 5px;
}
</style>
