<template>
  <div>
    <h1>순위 검색</h1>
    <h5>현재는 {{ display }} 위 까지만 노출됩니다.</h5>
    <div class="rank-box">
      <div v-if="isLoading" class="loadingbar">
        <LoadingBar />
      </div>
      <div class="search-form">
        <input v-model="storeQuery" placeholder="스토어명" />
        <input v-model="keywordQuery" placeholder="키워드" />
        <button class="search" @click="searchNaverShopping">조회하기</button>
      </div>
      <div v-if="searchResults.length > 0" class="search-result">
        <table>
          <thead>
            <tr>
              <th class="column-store">스토어</th>
              <th class="column-keyword">키워드</th>
              <th class="column-product">상품명</th>
              <th class="column-rank">순위</th>
              <th class="column-page-rank">페이지순위</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in searchResults" :key="index">
              <td class="column-store">{{ item.mallName }}</td>
              <td class="column-keyword">{{ keywordQuery }}</td>
              <td class="column-product">
              <img :src="item.image" :alt="item.title.replace(/<b>|<\/b>/g, '')" class="product-image"
                @mouseover="showZoomedImage(item.image, index)" @mouseout="hideZoomedImage" @mousemove="updateZoomedImagePosition" />
              <div v-if="zoomedImage && zoomedImagePosition === index" class="zoomed-image-container">
                <img :src="zoomedImage" class="zoomed-image" />
              </div>
                <a :href="item.item" target="_blank" rel="noopener noreferrer">
                  <span v-html="item.title.replace(/<b>|<\/b>/g, '')"></span></a>
              </td>
                <td class="column-rank">{{ item.rank + 1 }}</td>
              <td class="column-page-rank">{{ Math.floor(item.rank / 40) + 1 + ' 페이지' }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import LoadingBar from "@/components/LoadingBar.vue";

async function fetchResults(keyword, storeName, start) {
  const clientId = "SliOUwwJyx40KDhySwCf";
  const clientSecret = "r1Z3AkEecz";
  const apiUrl = "/api/v1/search/shop.json";
  
  try {
    const response = await axios.get(apiUrl, {
      headers: {
        "X-Naver-Client-Id": clientId,
        "X-Naver-Client-Secret": clientSecret,
      },
      params: {
        query: keyword,
        start: start,
        display: 100,
      },
    });

    const items = response.data.items.map((item, index) => {
      if (item.mallName === storeName) {
        item.rank = start - 1 + index;
      }
      return item;
    });

    return items;
  } catch (error) {
    console.error(error);
    return [];
  }
}

async function fetchAllResults(keyword, storeName) {
  let allItems = [];

  for (let start = 1; start <= 1000; start += 100) {
    const items = await fetchResults(keyword, storeName, start);
    const storeItems = items.filter(item => item.rank !== undefined);
    allItems = allItems.concat(storeItems);
  }

  return allItems;
}

export default {
  components: {
    LoadingBar,
  },

  data() {
    return {
      storeQuery: "",
      searchResults: [],
      display: 1000,
      zoomedImage: null,
      zoomedImagePosition: null,
      isLoading: false,
    };
  },
  methods: {
    updateZoomedImagePosition(event) {
      if (this.zoomedImage) {
        const zoomedImageContainer = this.$el.querySelector(".zoomed-image-container");
        zoomedImageContainer.style.left = event.clientX + 30 + "px";
        zoomedImageContainer.style.top = event.clientY - 210 + "px";
      }
    },
    showZoomedImage(image, index) {
      this.zoomedImage = image;
      this.zoomedImagePosition = index;
    },
    hideZoomedImage() {
       this.zoomedImage = null;
    },

    async searchNaverShopping() {
      if (!this.storeQuery || !this.keywordQuery) {
        return;
      }
      this.isLoading = true;
      const allItems = await fetchAllResults(this.keywordQuery, this.storeQuery);
      this.searchResults = allItems.slice(0, this.display);
      this.isLoading = false;
    },
  },
};
</script>

<style scoped>
.zoomed-image-container {
  position: fixed;
  left: 100px;
  top: 0;
  padding: 15px;
  background-color: rgba(255, 255, 255, 0.9);
  border: 1px solid #ccc;
  border-radius: 5px;
  z-index: 1000;
}
.zoomed-image {
  width: 200px;
  height: 200px;
}
.product-link {
  color: #343a40;
  text-decoration: none;
}
input {
  flex: 1;
  padding: 10px;
  height: 25px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 10px;
  max-width: 200px;
}
.product-image {
  width: 60px;
  height: 60px;
  margin-right: 30px;
  vertical-align: middle;
}
.loadingbar {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 5%;
  left: 50%;
  transform: translateX(-50%);
}
</style>

<style>
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f8f9fa;
  color: #343a40;
}
/* 테이블 스타일 */
.search-result table {
  width: calc(100% - 220px);
  border-collapse: collapse;
  font-size: 0.9rem;
  padding: 2rem;
  table-layout: center;
  margin: 0 10px 0 210px;
  margin-top: 30px;
}
th, td {
  padding: 15px;
  text-align: center;
  border-bottom: 1px solid #dddddd;
  background-color: #ffffff;
}
th {
  background-color: #3498db;
  color: #ffffff;
  text-align: center;
}
tbody tr:hover {
  background-color: #f5f5f5;
}
td:nth-child(3), th:nth-child(3) {
  text-align: left;
}
.column-store {
  width: 5%;
}
.column-keyword {
  width: 5%;
}
  .column-product {
  width: 30%;
}
.column-rank {
  width: 5%;
}
.column-page-rank {
  width: 5%;
}
</style>